@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.html{margin-top:0;height:0px;padding-top:0}.header{display:flex;align-items:center;justify-content:center;margin:0;height:100px;width:100%;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);box-shadow:0 5px 15px rgba(0,0,0,0.5);position:fixed;top:0;z-index:2}.header .header-title{margin:0;position:relative;z-index:3;color:dimgray;font-family:'Montserrat', sans-serif}

.search-form{display:flex;justify-content:center;align-items:center;position:relative;width:40%;margin-top:150px;padding-bottom:70px}.search-form .search-bar,.search-form .search-button{height:5rem;font-family:'Lato', sans-serif;border:0;color:#2f2f2f;font-size:15px}.search-form form{width:100%}.search-form .search-bar{border-radius:7px;width:90%;height:50px;background:#fff;padding:0 1.6rem;z-index:1;position:relative;transition:all 0.3s cubic-bezier(0, 0, 0.43, 1.49);transition-property:width, border-radius}.search-form input:not(:-ms-input-placeholder){border-radius:7px 0 0 7px;width:calc(100% - 140px)}.search-form input:not(:placeholder-shown){border-radius:7px 0 0 7px;width:calc(100% - 140px)}.search-form input:not(:-ms-input-placeholder)+button{display:block}.search-form input:not(:placeholder-shown)+button{display:block}.search-form .search-bar:focus{outline:none}.search-form .search-button{height:50px;display:none;position:absolute;top:0;right:0;width:100px;font-weight:bold;background:#8d8f8f;border-radius:0 .7rem .7rem 0}

#recipe-container{width:300px;height:100%;box-shadow:0px 0px 15px rgba(0,0,0,0.952);padding:25px;margin-bottom:50px;font-family:'Montserrat', sans-serif;background-color:#fff;display:flex;flex-direction:column}#recipe-container #ingredients.cool{max-height:500px;transition:1000ms}#recipe-container #ingredients{max-height:0;opacity:1;transition:700ms;overflow:hidden}#recipe-container .show-button{border-radius:7px;padding:10px 20px 10px 20px;background:#b4b3b3;box-shadow:yellow;border-style:none;margin-top:20px;outline:none}#recipe-container .title{height:60px}#recipe-container li{line-height:1.8}#recipe-container p{text-align:center;font-size:23px}#recipe-container img{border-radius:7px}

.recipe-list{display:flex;flex-wrap:wrap;justify-content:space-evenly}

:root{--color-dark: #555454}.app{background-image:url("https://www.vilohome.com/wp-content/uploads/2018/07/LIGHT-WOOD_MODERN_MT_TT_ANG.jpg");background-repeat:repeat;background-attachment:fixed;display:flex;flex-direction:column;align-items:center}

